import GetIcon from "./modules/GetIcon";
import styled from 'styled-components';
import { LoginDataSend } from "./modules/DataRouter";
import "./style.css";

// css
const CenterBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 100px;
`;
const Text = styled.p`
  text-align: ${(props) => props.align};
  width:331px;
  color: ${(props) => props.color};
  font-size: ${(props) => props.size};
`;
const InputContainer = styled.div`
  width: 285px;
  height: 47px;
  border-radius: 6px;
  background: #EAE8E8;
  margin-top: 20px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
`;
const InputImg = styled.img`
  width: 22px;
  margin-left: 20px;
`;
const InputTag = styled.input`
  width: 210px;
  margin-left: 10px;
  color: #939393;
  border: none;
  background-color: #EAE8E8;
  outline: none;
`
//
const Input = ({ icon, t, n, ph }) => {
  return (
    <InputContainer>
      <InputImg src={GetIcon(icon)}/>
      <InputTag type={t} name={n} placeholder={ph}/>
    </InputContainer>
  );
};


export const Login = () => {
  return (
    <div className="login">
      <div className="div">
        <CenterBox>
          <Text color={"#3252C2"} size={"15px"}><span style={{fontWeight:"bold"}}>CULISO</span> <span style={{color:"#4B66C8"}}>Account</span></Text>
          <div className="loginBG">
            <Text align={'center'} color={"black"} size={"15px"} style={{marginTop: '40px', fontFamily: 'LINESeedKR-Bd'}}>
                   ,<br/>
                !<br/>
                 .
            </Text>
            <form onSubmit={LoginDataSend} method="post">
              <Input icon={"profile-gray.png"} t={"text"} n={"id"} ph={""}/>
              <Input icon={"closed-padlock-gray.png"} t={"password"} n={"pw"} ph={""}/>
              <label style={{width:"285px", display: "flex", alignItems: "center"}}>
                <input type="checkbox" className="checkBox"/><span style={{color: "#8a8a8a", fontSize: "12px", marginLeft: "8px",}}> </span>
              </label>
              <input className="loginSubmit" type="submit" value={""}/>
            </form>
            <div className="aDiv">
              <a href="/signUp"> </a>
              <a href="#"> </a>
              <a href="#"> </a>
            </div>
          </div>
        </CenterBox>
      </div>
    </div>
  );
};
